<template>
    <b-overlay :show="isLoading" >
        <form @submit.prevent="EditLine()">
            <div class="mb-3">
                                    
                        <div class="form-group">
                            <label>transactions_totals </label>
                                                            <input type="text" :class="errors.transactions_totals?'form-control is-invalid':'form-control'"
                                       v-model="form.transactions_totals">
                            
                            <div class="invalid-feedback" v-if="errors.transactions_totals">
                                <template v-for=" error in errors.transactions_totals" >  {{error[0]}}</template>

                            </div>
                        </div>
                    
                                    
                        <div class="form-group">
                            <label>transactions_heures </label>
                                                            <input type="text" :class="errors.transactions_heures?'form-control is-invalid':'form-control'"
                                       v-model="form.transactions_heures">
                            
                            <div class="invalid-feedback" v-if="errors.transactions_heures">
                                <template v-for=" error in errors.transactions_heures" >  {{error[0]}}</template>

                            </div>
                        </div>
                    
                                    
                                    
                        <div class="form-group">
                            <label>date </label>
                                                            <input type="text" :class="errors.date?'form-control is-invalid':'form-control'"
                                       v-model="form.date">
                            
                            <div class="invalid-feedback" v-if="errors.date">
                                <template v-for=" error in errors.date" >  {{error[0]}}</template>

                            </div>
                        </div>
                    
                                    
                        <div class="form-group">
                            <label>pointeuse </label>
                                                            <input type="text" :class="errors.pointeuse?'form-control is-invalid':'form-control'"
                                       v-model="form.pointeuse">
                            
                            <div class="invalid-feedback" v-if="errors.pointeuse">
                                <template v-for=" error in errors.pointeuse" >  {{error[0]}}</template>

                            </div>
                        </div>
                    
                

                                </div>

            <div class="d-flex justify-content-between">
                <button type="submit" class="btn btn-primary" >
                    <i class="fas fa-floppy-disk"></i> Mettre à jour
                </button>
                <button type="button" class="btn btn-danger" @click.prevent="DeleteLine()">
                    <i class="fas fa-close"></i> Supprimer
                </button>
            </div>
        </form>
    </b-overlay>
</template>

<script>
    import $ from 'jquery'
    import Files from "@/components/Files.vue"
    import CustomSelect from "@/components/CustomSelect.vue"
    export default {
        name: 'EditPointeusestransactions',
        components: { CustomSelect,Files },
        props: ['data','gridApi','modalFormId',
                    ],
        data () {
            return {
                errors: [],
                isLoading: false,
                form: {
                    
                        transactions_totals:"",
                    
                        transactions_heures:"",
                    
                        transactions_id:"",
                    
                        date:"",
                    
                        pointeuse:"",
                                    }
            }
        },

        mounted () {
            this.form = this.data
                                                                                                                                                                },
        methods: {

            EditLine () {
                this.isLoading = true
                this.axios.post('/api/pointeusestransactions/' + this.form.id + '/update', this.form).then(response => {
                    this.isLoading = false
                    this.gridApi.applyServerSideTransaction({
                        update: [
                            response.data
                        ],
                    });
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    this.errors = error.response.data.errors
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de l\'enregistrement')
                })
            },
            DeleteLine () {
                this.isLoading = true
                this.axios.post('/api/pointeusestransactions/' + this.form.id + '/delete').then(response => {
                    this.isLoading = false

                    this.gridApi.applyServerSideTransaction({
                        remove: [
                            this.form
                        ]
                    });
                    this.gridApi.refreshServerSide()
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    console.log(error.response.data)
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de la suppression')
                })
            },
        }
    }
</script>
